<template>
  <main class="container-fluid">
    <form class="form">
      <h3></h3>
      <h4 class="text-green mb-4"></h4>
      <div class="form-group">
        <label for="name">
          
          <span class="waring-block d-inline-block">
            <span class="text"><span class="text-red"></span></span>
          </span>
        </label>
        <input type="text" id="name" class="form-control"
        v-model="form.name"
        placeholder="">
      </div>
      <div class="form-group">
        <label for="identity-id">ID</label>
        <input type="text" id="identity-id" class="form-control is-invalid"
        v-model="form.identityID"
        placeholder="">
        <div class="invalid-feedback">
          ID
        </div>
      </div>
      <h4 class="text-green mb-4">
        
        <span class="waring-block d-inline-block">
          <span class="text"><span class="text-red"></span></span>
        </span>
      </h4>
      <div class="form-group">
        <label for="line-id">LINE ID</label>
        <input type="text" id="line-id" class="form-control"
        v-model="form.lineid"
        placeholder=" LINE ID">
      </div>
      <div class="form-group">
        <label for="mobile"></label>
        <input type="tel" id="mobile" class="form-control"
        @input="numberMobile"
        v-model="form.mobile"
        placeholder="0912345678">
      </div>
      <div class="form-group">
        <label for="phone"></label>
        <input type="tel" id="phone" class="form-control form-control-inline form-short"
        @input="numberPhone0"
        v-model="form.phone[0]"
        placeholder="">
        <span class="text">-</span>
        <input type="tel" class="form-control form-control-inline"
        @input="numberPhone1"
        v-model="form.phone[1]"
        placeholder="">
      </div>
      <div class="form-group">
        <label for="office"></label>
        <input type="tel" id="office" class="form-control form-control-inline form-short"
        @input="numberOffice_phone0"
        v-model="form.office_phone[0]"
        placeholder="">
        <span class="text">-</span>
        <input type="tel" class="form-control form-control-inline"
        @input="numberOffice_phone1"
        v-model="form.office_phone[1]"
        placeholder="">
        <span class="text">-</span>
        <input type="tel" id="office" class="form-control form-control-inline form-short"
        @input="numberOffice_phone2"
        v-model="form.office_phone[2]"
        placeholder="">
      </div>
      <div class="action two-buttons mt-5">
        <button type="reset" class="btn btn-default"></button>
        <button type="submit"
        class="btn btn-primary"
        @click.stop.prevent="doSubmit()"></button>
      </div>
    </form>

    <warning-modal
    :message="''"
    :open="warningModalOpen"
    @close="panelClose('warningModalOpen')"/>

  </main>
</template>

<script>
import {mapActions} from 'vuex'
import WarningModal from '../components/WarningModal.vue'
import {commonMixin} from '../../mixins/common'

export default {
  name: 'CustomerCreate',
  mixins: [commonMixin],
  components: { WarningModal },
  data () {
    return {
      isValid: false,
      warningModalOpen: false,
      form: {
        name: '',
        identityID: '',
        lineid: '',
        mobile: '',
        phone: ['', ''],
        office_phone: ['', '', '']
      }
    }
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),

    //  input 
    numberMobile(){
      this.form.mobile = this.form.mobile.replace(/[^\d]/g,'');
    },
    numberPhone0(){
      this.form.phone[0] = this.form.phone[0].replace(/[^\d]/g,'');
    },
    numberPhone1(){
      this.form.phone[1] = this.form.phone[1].replace(/[^\d]/g,'');
    },
    numberOffice_phone0(){
      this.form.office_phone[0] = this.form.office_phone[0].replace(/[^\d]/g,'');
    },
    numberOffice_phone1(){
      this.form.office_phone[1] = this.form.office_phone[1].replace(/[^\d]/g,'');
    },
    numberOffice_phone2(){
      this.form.office_phone[2] = this.form.office_phone[2].replace(/[^\d]/g,'');
    },

    doSubmit () {
      if (
        this.form.name==='' ||
        (this.form.lineid==='' && this.form.mobile==='' && this.form.phone && this.form.office_phone)
      ) {
        this.isValid = true
        this.panelOpen('warningModalOpen')
      } else {
        this.$router.push('/customer/1')
        this.$toast.info({title: ''})
      }
    }
  },
  mounted () {
  }
}
</script>
